.house-container {
    display: flex;
    flex-wrap: wrap;
  }
  
  .house {
    width: 90%;
    height: 80vh;
    margin-left: 5%;
    margin-top: 50px;
    margin-bottom: 20px;
    box-sizing: border-box;
    padding: 10px;
    border: 2px solid aliceblue;
    border-radius: 25px;
  }
  
  .house-content {
    align-items: center;
    justify-content: space-between;
    height: 100%;
    padding: 20px;
  }
  
  #box{
    display: flex;
  }


  .house-info {
    align-items: center;
  }
  
  .house-name {
    font-size: 40px;
    margin-bottom: 30px;
    color: white;
  }
  
  .house-image {
    width: 700px; /* 设置图片宽度 */
    height:450px; /* 设置图片高度 */
  }
  
  .house-description {
    margin: 15px 30px;
    font-size: 28px;
    color: white;
  }
  