a{
    text-decoration: none;
    color: inherit;
}

*{
    box-sizing: border-box;
}

#headerNav{
    display: table;
    padding: 0px 2%;
    background: transparent;
    align-items: center;
}

.headerUl{
	list-style-type: none;
	display: table-row;
}

.headerList {
    color:rgba(240, 248, 255, 0.75);
	display: table-cell;
	font-size: 18px;
	width: 110px;
    font-weight: 400;
    position: relative;
    text-align: center;
    line-height: 72px;
    opacity: 0;
    transition: .3s;
    animation: slideTop 1s ease forwards;
}

.headerList:nth-of-type(1){
    animation-delay: .2s;
}

.headerList:nth-of-type(2){
    animation-delay: .4s;
}

.headerList:nth-of-type(3){
    animation-delay: .6s;
}

.headerList:nth-of-type(4){
    animation-delay: .8s;
}

.headerList:hover {
    color:rgb(240, 248, 255);
}

#divLine{
    background-color: rgb(251, 211, 130);
    height: 1.5px;
    width: 240px;
    position: absolute;
    left: 4.5%;
    top: 60px;
    animation: slideRight 1s ease forwards;
    animation-delay: .8s;
    opacity: 0;
}

.headerDiv{
    position: absolute;
    left: 10px;
    top: 61.5px;
    width: 880px;
    height: 350px;
    display: none;
    background-color: rgb(121, 121, 121);
    font-size: 0px;
    border-radius: 5px;
    z-index: 1;
    color: aliceblue;
    transition: .5s;
}

.headerDiv p{
    text-align: center;
    animation: font 1s ease-in-out forwards;
    font-size: 32px;
    animation-delay: .5s;
    padding-top: 20px;
    opacity: 0;
    padding-bottom: 10px;
}

.headerDiv li{
    animation: font 1s ease-in-out forwards;
    animation-delay: .5s;
    font-size: 20px;
    opacity: 0;
    margin-left: 10px;
    list-style-type: disc;
    padding: 5px;
    margin-left: 30px;
}

.headerList:hover + .headerDiv{
    display: block;
    animation: whenHover_Div 0.6s ease-in-out forwards;
}

@keyframes font{
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

@keyframes slideRight{
    0% {
        transform: translateX(-100px);
        opacity: 0;
    }

    100% {
        transform: translateX(0);
        opacity: 1;
    }
}

@keyframes whenHover_Div{
    0% {
        height: 0;
    }
    
    100% {
        height: 350px;
    }
}

.headerDiv:hover{
    display: block;
}

.search_box{
    height: 40px;
    position: absolute;
    right: 3%;
    top: 2.3%;
    border: 2px solid rgb(26, 248, 222);
    border-radius: 40px;
    background-color: rgba(255, 255, 255, 0.82);
}

.search_box:hover .search_txt{
    width: 250px;
    padding: 0 8px;
}

.search_txt{
    border: none;
    background: none;
    outline: none;
    float:left;
    padding: 0;
    font-size: 16px;
    font-weight: 500;
    transition: all .4s;
    width: 0;
    line-height: 40px;
}

#search{
    text-decoration: none;
    width: 40px;
    border-radius: 50%;
    display: flex;
    transition: all .4s;
    align-items: center;
    justify-content: center;
    position: relative;
}

.search_box img{
    display: block;
    margin-top: 5px;
    margin-right: 1px;
    width: 30px;
    height: 30px;
}