.container{
    overflow: hidden;
    width: 100%;
    height: 500px;
    /* box-shadow: 0 30px 50px aliceblue; */
}

#slide{
    width:max-content;
    margin-top: 50px;
    overflow: hidden;
}

.item{
    width: 200px;
    height: 300px;
    background-position: 50% 50%;
    display: inline-block;
    background-size: cover;
    position: absolute;
    border-radius: 20px;
    box-shadow: 0 20px 30px #505050;
    transform: translateY(-50%);
    transition: .5s;
}

.item:nth-child(1),
.item:nth-child(2){
    top: 780px;
    left: 0;
    width: 100%;
    height: 100%;
    transform: translateY(0);
    border-radius: 0;
    box-shadow: none; 
}

.item:nth-child(3){
    top: 1300px;
    left: 65%;
}

.item:nth-child(4){
    top: 1300px;
    left: calc(65% + 220px);
}

.item:nth-child(5){
    top: 1300px;
    left: calc(65% + 440px);
}

.item:nth-child(6){
    top: 1300px;
    left: calc(65% + 660px);
}

.content{
    width: 350px;
    position: absolute;
    top: 65%;
    left: 100px;
    font-family: system-ui;
    display: none;
}

.item:nth-child(2) .content{
    display: block;
}

.name{
    font-size: 30px;
    font-weight: bold;
    opacity: 0;
    width: 400px;
    animation: show 1s ease-in-out 1 forwards;
    color: rgb(0, 0, 0);
    background-color: rgba(240, 248, 255, 0.85);
    border-radius: 5px;
    padding-left: 10px;
    padding: 5px;
}

.description{
    margin: 20px 0 ;
    opacity: 0;
    animation: show 1s ease-in-out .3s 1 forwards;
    font-size: 16px;
    width: 400px;
    text-align: justify;
    color: rgb(0, 0, 0);
    background-color: rgba(240, 248, 255, 0.85);
    border-radius: 5px;
    padding-left: 10px;
    padding: 5px;

}

.item button{
    padding: 10px 20px;
    border: none;
    opacity: 0;
    animation: show 1s ease-in-out .5s 1 forwards;
    font-size: 15px;
    border-radius: 15px;
    background-color: rgba(255, 255, 255, 0.85);
    color: rgb(0, 0, 0);
}

@keyframes show{
    from{
        opacity: 0;
        transform: translateY(100px);
        filter: blur(5px);
    }

    to{
        opacity: 1;
        transform: translateY(0);
        filter: blur(0);
    }
}

.buttons{
    width: 100%;
    text-align: center;
    position: absolute;
    top: 1470px;
}

.but{
    display: inline-block;
    width: 50px;
    height: 50px;
    line-height: 50px;
    text-align: center;
    color: rgb(0, 0, 0);
    background-color: #d0ccc4;
    font-size: 36px;
    border-radius: 50%;
    font-weight: bold;
    border: 2px solid #555;
    margin:0 15px;
    transition: .5s;
}

.but:hover{
    cursor: pointer;
    background-color: #403f3e;
    color: aliceblue;
    box-shadow: 1px 0 5px #f8c971;
}