
.header {
    position: absolute;
    top: 1635px;
    left: 0;
    width: 100%;
    text-align: center;
    background:#1f242d;
    color: rgb(255, 255, 255);
}

.inner-header {
    margin-top: -35px;
    width: 100%;
    padding-bottom: 20px;
}

#useP{
    font-size: 32px;
    padding-bottom: 10px;
    letter-spacing: 0.5px;
}

#small{
    font-size: 18px;
}

.waves {
    position: relative;
    width: 100%;
    height: 10vh;
    min-height: 100px;
    max-height: 150px;
}

.parallax>use {
    animation: move-forever 25s cubic-bezier(.55, .5, .45, .5) infinite;
}

.parallax>use:nth-child(1) {
    animation-delay: -2s;
    animation-duration: 7s;
}

.parallax>use:nth-child(2) {
    animation-delay: -3s;
    animation-duration: 10s;
}

.parallax>use:nth-child(3) {
    animation-delay: -4s;
    animation-duration: 13s;
}

.parallax>use:nth-child(4) {
    animation-delay: -5s;
    animation-duration: 20s;
}

@keyframes move-forever {
    0% {
        transform: translate3d(-90px, 0, 0);
    }

    100% {
        transform: translate3d(85px, 0, 0);
    }
}

@media (max-width: 768px) {
    .waves {
        height: 40px;
        min-height: 40px;
    }

    h1 {
        font-size: 24px;
    }
}    