.home_content{
    max-width: 700px;

}
.home_content h3{
    font-size: 28px;
    font-weight: 700;
    opacity: 0;
    animation: slideBottom 1s ease forwards;
    animation-delay: .7s;
}

.home_content h3:nth-of-type(2){
    opacity: 0;
    animation: slideTop 1s ease forwards;
    animation-delay: .7s;
    transition: 1s;
}

#writer-container{
    display: inline-block;
}

#writer{
    color: #d0c3a9;
    display: inline-block;
}

#cursor {
    display: inline-block;
    width: 2px;
    height: 30px;
    background-color: #ffffff;
    margin-left: 2px;
    animation: blink .7s infinite;
}

@keyframes blink {
    0%, 100% {
      opacity: 1;
    }
    50% {
      opacity: 0;
    }
  }

.home_content h1{
    font-size: 52px;
    font-weight: 700;
    opacity: 0;
    animation: slideRight 1s ease forwards;
    animation-delay: 1s;
}

.home_content p{
    font-size: 16px;
    opacity: 0;
    animation: slideLeft 1s ease forwards;
    animation-delay: .7s;
    margin-bottom: 3px;
    color: #fff;
}

.home_img{
    max-width: 450px;
    border-radius: 50%;
    width: 400px;
    height: 400px;
    box-shadow: 0 0 20px #b7b2a9;
    opacity: 0;
    animation: zoomIn 1s ease forwards,floatImage 4s ease-in-out infinite;
    animation-delay: 1.3s,3s;
}

.home_img:hover{
    margin-right: -20px;
    width: 450px;
    height: 450px;
    transition: .2s;
    z-index: -2;
}

.social-media a {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    background: transparent;
    border: 2px solid #b7b2a9;
    font-size: 20px;
    text-decoration: none;
    border-radius: 50%;
    color: #b7b2a9;
    margin: 20px 15px 5px 0;
    transition: .5s ease;
    opacity: 0;
    animation: slideLeft 1s ease forwards;
}

.social-media a:nth-child(1){
    animation-delay: 0.6s;
}

.social-media a:nth-child(2){
    animation-delay: 0.8s;
}

.social-media a:nth-child(3){
    animation-delay: 1s;
}

.social-media a:nth-child(4){
    animation-delay: 1.2s;
}

.social-media a:hover{
    background: #b7b2a9;
    color: #1f242d;
    box-shadow: 0 0 20px #b7b2a9;
}

.btn{
    display: inline-block;
    position: relative;
    z-index: 1;
    overflow: hidden;
    text-decoration: none;
    padding: 12px 28px;
    background: #cdc8be;
    border-radius: 40px;
    box-shadow: 0 0 10px #b7b2a9;
    font-size: 16px;
    color: #21262f;
    letter-spacing: 1px;
    font-weight: 600;
    transition: 0.5s ease;
    margin-top: 18px;
    opacity: 0;
    animation: slideTop 1s ease forwards;
    animation-delay: 1.1s;
}

.btn::before,
.btn::after{
    content: "";
    position: absolute;
    top: -30px;
    z-index: -1;
    width: 200%;
    aspect-ratio: 1;
    border: none;
    border-radius: 40%;
    background-color: rgba(0,0,255,0.45);
    transition: 4s;
}

.btn::before{
    left: -80%;
    transform: translate3d(0,5em,0) rotate(-340deg);
}

.btn::after{
    right: -80%;
    transform: translate3d(0,5em,0) rotate(-390deg);
}

.btn:hover
.btn:focus{
    color: white;
    box-shadow: 1px 0 50px #b7b2a9;
}

.btn:hover::before,
.btn:hover::after,
.btn:focus::after,
.btn:focus::before{
    transform: none;
    background-color: rgba(224, 202, 166, 0.75);
}

@keyframes slideBottom{
    0% {
        transform: translateY(-100px);
    }

    100% {
        transform: translateY(0);
        opacity: 1;
    }
}

@keyframes slideTop{
    0% {
        transform: translateY(100px);
    }

    100% {
        transform: translateY(0);
        opacity: 1;
    }
}

@keyframes slideRight{
    0% {
        transform: translateX(-100px);
    }

    100% {
        transform: translateX(0);
        opacity: 1;
    }
}

@keyframes slideLeft{
    0% {
        transform: translateX(100px);
    }

    100% {
        transform: translateX(0);
        opacity: 1;
    }
}

@keyframes zoomIn{
    0% {
        transform: scale(0);
    }

    100% {
        transform: scale(1);
        opacity: 1;
    }
}

@keyframes floatImage{
    0% {
        transform: translateY(0);
    }

    50% {
        transform: translateY(-30px);
    }

    100% {
        transform: translateY(0);
    }
}
